<template>
    <VStack>
        <HStack class="admin-top-bar"><p style="margin-left: 20px;">{{ currentUser }}</p><SpacerVue/><h1 style="color: #1a4169; font-weight: bolder; margin-right: 15px;" @click="pushData">Salva</h1><h1  style="color: #1a4169; font-weight: bolder;" @click="onAddButtonTap">Aggiungi</h1><a href="/logout">Esci</a></HStack>
        <SuccessBanner v-show="pushDataSuccessMessageIsVisible"><template v-slot:body>{{ pushDataSuccessBannerMessage }}</template></SuccessBanner>
        <ErrorBanner v-show="pushDataErrorMessageIsVisible"><template v-slot:body>{{ pushDataErrorBannerMessage }}</template></ErrorBanner>  
        <AdminGridCell v-for="cell in cells" :id="cell" :cells="cells" :key="cell" @moveCell="moveCell" @click="this.showModal(cell)" :title="data.find(x => x.id == cell).title" :image="data.find(x => x.id == cell).fixed_image" :on_home="data.find(x => x.id == cell).on_home" class="admin-grid-cell" @deleteData="deleteData" @setFixedData="setFixedData"/>
        <form action="/change-password" method="POST" class="admin-form">
            <label>Modifica la tua password</label> <br> <br>
            <ErrorBanner v-show="changePasswordErrorMessageIsVisible"><template v-slot:body>{{ changePasswordErrorBannerMessage }}</template></ErrorBanner>  
            <SuccessBanner v-show="changePasswordSuccessMessageIsVisible"><template v-slot:body>{{ changePasswordSuccessBannerMessage }}</template></SuccessBanner> 
            <input type="password" name="pw" id="" placeholder="Nuova Password"> <br> <br>
            <input type="submit" value="Modifica"> <br> <br>
        </form>
        <form action="/register" method="POST" class="admin-form">
            <label for="">Registra un nuovo admin</label> <br> <br>
            <ErrorBanner v-show="registerErrorMessageIsVisible"><template v-slot:body>{{ registerErrorBannerMessage }}</template></ErrorBanner> 
            <SuccessBanner v-show="registerSuccessMessageIsVisible"><template v-slot:body>{{ registerSuccessBannerMessage }}</template></SuccessBanner> 
            <input type="text" name="nm" id="" placeholder="Nome Utente"> <br> <br>
            <input type="password" name="pw" id="" placeholder="Password"> <br> <br>
            <input type="submit" value="Aggiungi"> <br>
        </form>
        <form action="/delete-account" method="POST" >
            <input type="submit" value="Elimina questo account" class="delete-button">
        </form>
        <ModalVue
            v-show="isModalVisible"
            @close = "this.closeModal()" 
            style="overflow-y: scroll; max-height: 100vh;"
        >
            <template v-slot:body>
                <VStack>
                    <HStack>
                        <input type="text" placeholder="Nome" v-model="modalData.title">
                        <SpacerVue/>
                        <input type="text" placeholder="Prezzo" v-model="modalData.price">
                        <div style="color: #1a4169;font-weight: 900;font-size:36px;">€</div>
                    </HStack>

                    <textarea name="" id="" cols="30" rows="10" placeholder="Descrizione" v-model="modalData.description"></textarea>

                    <HStack>
                        <SpacerVue/>
                        <SegmentedControl class="segmented-control" :isModalVisible="isModalVisible" :value="modalData.category" @update="(newData) => modalData.category = newData"/>
                        <SpacerVue/>
                    </HStack>
                    <HStack>
                        <SpacerVue/>
                        <CustomSelect
                            :options="['fa-solid fa-bath','fa-solid fa-up-right-and-down-left-from-center', 'fa-solid fa-bed','fa-solid fa-tents','fa-solid fa-stairs','fa-solid fa-table']"
                            class="select"
                            :selection="selection1"
                            number="1"
                            @input="onSelectChange"
                        />
                        <input type="number" min="0"  id="" v-model="selectNumber1">
                        <SpacerVue/>
                        <CustomSelect
                            :options="['fa-solid fa-bath','fa-solid fa-up-right-and-down-left-from-center', 'fa-solid fa-bed','fa-solid fa-tents','fa-solid fa-stairs','fa-solid fa-table']"
                            class="select"
                            :selection="selection2"
                            number="2"
                            @input="onSelectChange"
                        />
                        <input type="number" min="0"  id="" v-model="selectNumber2">
                        <SpacerVue/>
                        <CustomSelect
                            :options="['fa-solid fa-bath','fa-solid fa-up-right-and-down-left-from-center', 'fa-solid fa-bed','fa-solid fa-tents','fa-solid fa-stairs','fa-solid fa-table']"
                            class="select"
                            :selection="selection3"
                            number="3"
                            @input="onSelectChange"
                        />
                        <input type="number" min="0"  id="" v-model="selectNumber3">
                        <SpacerVue/>
                    </HStack>

                    <h3>Caratteristiche</h3>
                    <VStack class="info-table">
                        <HStack><strong>Tipologia</strong><SpacerVue/><input type="text" v-model="modalData.tipology"></HStack>
                        <HStack><strong>Città</strong><SpacerVue/><input type="text" v-model="modalData.city"></HStack>
                        <HStack><strong>Indirizzo</strong><SpacerVue/><input type="text" v-model="modalData.address"></HStack>
                        <HStack><strong>Zona</strong><SpacerVue/><input type="text" v-model="modalData.zone"></HStack>
                        <HStack v-for="feature in modalData.others" :key="feature"><strong><input type="text" v-model="feature[0]"></strong><SpacerVue/><input type="text" v-model="feature[1]"></HStack>
                        <HStack><strong><form @submit.prevent="addFeature" id="otherFeatureTitleForm"><input type="text" v-model="otherFeatureTitle" ref="otherFeatureTitleInput" placeholder="Altro"></form></strong><SpacerVue/><form @submit.prevent="addFeature" id="otherFeatureValueForm"><input type="text" v-model="otherFeatureValue" ref="otherFeatureValueInput" placeholder="Altro"></form></HStack>
                    </VStack>

                    <input type="text" placeholder="Link Youtube" class="link-input" v-model="modalData.video">
                    <input type="text" placeholder="Link Google Maps" class="link-input" v-model="modalData.maps">
                    <input type="text" placeholder="Link Google StreetView" class="link-input" v-model="modalData.street_view">
                    <HStack><input type="file" name="" id="" accept=".pdf" style="margin-top: 20px;" @change="updateDataPdf"> <p>File Caricato:{{ modalData.pdf }}</p></HStack>

                    <Row container :gutter="25" class="row" style="margin-top: 30px;">
                        <Column :xs="12" :sm="6"  :md="6" :lg="6" :xl="4" v-for="image in modalData.images" :key="image"> <AdminImage :image="image" :blob="false" @onModalImagePinTap="onModalImagePinTap" @onModalImageTrashTap="onModalImageTrashTap"/></Column>
                        <Column :xs="12" :sm="6"  :md="6" :lg="6" :xl="4" v-for="image in cacheImages.filter((element) => element.id == modalData.id)" :key="image"> <AdminImage :image="image.url" :blob="true" @onModalImagePinTap="onModalImagePinTap" @onModalImageTrashTap="onModalImageTrashTap"/></Column>
                        
                        <Column :xs="12" :sm="6"  :md="6" :lg="6" :xl="4"> <AdminImageAddButton @updateCacheImages="updateCacheImages" @updateDataImages="updateDataImages" :superCacheImages="cacheImages"/></Column>
                    </Row>


                    
                </VStack>
            </template>
        </ModalVue>
    </VStack>
</template>

<script>
import AdminGridCell from "@/components/AdminGridCell.vue";
import VStack from "@/components/VStack.vue";
import ModalVue from "@/components/Modal.vue";
import HStack from "@/components/HStack.vue";
import SegmentedControl from "@/components/SegmentedControl.vue";
import SpacerVue from "@/components/SpacerVue.vue";
import CustomSelect from "@/components/CustomSelect.vue"
import AdminImage from "@/components/AdminImage.vue"
import { Row , Column } from "vue-grid-responsive";
import AdminImageAddButton from "@/components/AdminImageAddButton.vue";
import axios from "axios";
import ErrorBanner from "@/components/ErrorBanner.vue"
import SuccessBanner from "@/components/SuccessBanner.vue";
import FormData from "form-data"


export default {
    watch: {
        selectNumber1(newValue) {
            switch(this.selection1) {
                case "fa-solid fa-bath":
                    this.modalData.bath_rooms = newValue
                    break
                case "fa-solid fa-up-right-and-down-left-from-center":
                    this.modalData.size = newValue
                    break
                case "fa-solid fa-bed":
                    this.modalData.bed_rooms = newValue
                    break
                case "fa-solid fa-tents":
                    this.modalData.green_houses = newValue
                    break
                case "fa-solid fa-stairs":
                    this.modalData.floor = newValue
                    break
                case "fa-solid fa-table":
                    this.modalData.rooms = newValue
                    break
            }
        },
        selectNumber2(newValue) {
            switch(this.selection2) {
                case "fa-solid fa-bath":
                    this.modalData.bath_rooms = newValue
                    break
                case "fa-solid fa-up-right-and-down-left-from-center":
                    this.modalData.size = newValue
                    break
                case "fa-solid fa-bed":
                    this.modalData.bed_rooms = newValue
                    break
                case "fa-solid fa-tents":
                    this.modalData.green_houses = newValue
                    break
                case "fa-solid fa-stairs":
                    this.modalData.floor = newValue
                    break
                case "fa-solid fa-table":
                    this.modalData.rooms = newValue
                    break
            }
        },
        selectNumber3(newValue) {
            switch(this.selection3) {
                case "fa-solid fa-bath":
                    this.modalData.bath_rooms = newValue
                    break
                case "fa-solid fa-up-right-and-down-left-from-center":
                    this.modalData.size = newValue
                    break
                case "fa-solid fa-bed":
                    this.modalData.bed_rooms = newValue
                    break
                case "fa-solid fa-tents":
                    this.modalData.green_houses = newValue
                    break
                case "fa-solid fa-stairs":
                    this.modalData.floor = newValue
                    break
                case "fa-solid fa-table":
                    this.modalData.rooms = newValue
                    break
            }
        }
    },
    mounted()
    {
        this.fetchFlashData()
        this.fetchData()
    },
    components: {
    AdminGridCell,
    VStack,
    ModalVue,
    HStack,
    SegmentedControl,
    SpacerVue,
    CustomSelect,
    AdminImage,
    Row,
    Column,
    AdminImageAddButton,
    ErrorBanner,
    SuccessBanner
    },
    data() {
        return {
            cells:[],
            isModalVisible:false,
            otherFeatureTitle:"",
            otherFeatureValue:"",
            selectNumber1:0,
            selection1:"",
            selectNumber2:0,
            selection2:"",
            selectNumber3:0,
            selection3:"",
            modalData:{
                address:"",
                bath_rooms:"",
                rooms:"",
                green_houses:"",
                floor:"",
                category:"",
                bed_rooms:"",
                city:"",
                description:"",
                fixed_image:"",
                id:undefined,
                images:[],
                maps:"",
                on_home:undefined,
                others:[],
                pdf:"",
                price:undefined,
                size:undefined,
                street_view:"",
                tipology:"",
                title:"",
                video:"",
                zone:""
            },
            currentUser:"",
            registerErrorBannerMessage:"",
            changePasswordErrorBannerMessage:"",
            registerSuccessBannerMessage:"",
            changePasswordSuccessBannerMessage:"",
            pushDataSuccessBannerMessage:"",
            pushDataErrorBannerMessage:"",
            data:[],
            cacheImages:[],
            dataImages: new FormData(),
            serverImages:[],
            dataPdf:new FormData(),
            serverPdf:[]
            
            
        }
    },
    methods: {
        //AdminGridCell methods
        moveCell(src, dst) {
            if (src == dst) {return}
            const cell = this.cells[src]
            this.cells.splice(dst,0,cell)
            if (src > dst) {
                //remove at src + 1
                this.cells.splice(src + 1,1)
            } else if (src < dst) {
                //remove at src
                this.cells.splice(src , 1)
            }
        },
        //Modal methods
        showModal(id){
            if (id != undefined) {
                //populate modal with info of element at id 
                const item = this.data.find(x => x.id == id)
                this.modalData = item
                console.log(this.modalData)
                var selections = []
                if (this.modalData.rooms > 0) {
                    selections.push(["fa-solid fa-table",this.modalData.rooms])
                } 
                if (this.modalData.green_houses > 0) {  
                    selections.push(["fa-solid fa-tents",this.modalData.green_houses])
                }
                if (this.modalData.floor > 0) {
                    selections.push(["fa-solid fa-stairs",this.modalData.floor])
                }
                if (this.modalData.bath_rooms > 0) {
                    selections.push(["fa-solid fa-bath",this.modalData.bath_rooms])
                }
                if (this.modalData.bed_rooms > 0) {
                    selections.push(["fa-solid fa-bed",this.modalData.bed_rooms])
                }
                if (this.modalData.size > 0) {
                    selections.push(["fa-solid fa-up-right-and-down-left-from-center",this.modalData.rooms])
                }
                for (let i = 0 ; i < selections.length; i++) {
                    switch(i) {
                        case 0:
                            console.log("i == 0")
                            this.selection1 = selections[i][0]
                            this.selectNumber1 = selections[i][1]
                            break
                        case 1:
                            console.log("i == 0")
                            this.selection2 = selections[i][0]
                            this.selectNumber2 = selections[i][1]
                            break
                        case 2:
                            console.log("i == 0")
                            this.selection3 = selections[i][0]
                            this.selectNumber3 = selections[i][1]
                            break
                    }
                }
            } else {
                const item = {}
                const indexs = this.data.map(e => e.id)
                const max = indexs.length > 0 ? Math.max(...indexs) : 0
                console.log("max:".concat(max +1))
                item.id = max + 1
                item.address = ""
                item.bath_rooms = 0
                item.bed_rooms = 0
                item.category = "vendita"
                item.city = ""
                item.description = ""
                item.fixed_image = ""
                item.floor = 0
                item.green_houses = 0
                item.images = []
                item.maps = ""
                item.on_home = false
                item.others = []
                item.pdf = ""
                item.price = 0
                item.rooms = 0
                item.size = 0
                item.street_view = ""
                item.tipology = ""
                item.title = ""
                item.zone = ""
                item.video = ""

                this.modalData = item
                
            }
            //show modal
            this.isModalVisible = true
        },
        closeModal() {
            var selections = []
            selections.push(this.selection1)
            selections.push(this.selection2)
            selections.push(this.selection3)
            if (!selections.includes("fa-solid fa-table")) {
                this.modalData.rooms = 0
            }
            if (!selections.includes("fa-solid fa-tents")) {
                this.modalData.green_houses = 0
            }
            if (!selections.includes("fa-solid fa-stairs")) {
                this.modalData.floor = 0
            }
            if (!selections.includes("fa-solid fa-bath")) {
                this.modalData.bath_rooms = 0
            }
            if (!selections.includes("fa-solid fa-bed")) {
                this.modalData.bed_rooms = 0
            }
            if (!selections.includes("fa-solid fa-up-right-and-down-left-from-center")) {
                this.modalData.size = 0
            }

            const i = this.data.map(e => e.id).indexOf(this.modalData.id)
            console.log(i)
            if (i != -1) {
                this.data[i] = this.modalData
            } else {
                if (this.modalData.title != "") {
                    this.data.push(this.modalData)
                    this.cells.push(this.modalData.id)
                }
                
                console.log("New Item added")
            }
            
            //hyde modal
            this.isModalVisible = false
        },
        updateCacheImages(images) {
            for (const image of images) {
                    var isInImages = false
                    for (const cacheImage of this.cacheImages) {
                        if (cacheImage.name == image.name) {
                            isInImages = true
                        }
                    }
                    if (!isInImages) {
                        var cacheImage = image
                        cacheImage.id = this.modalData.id
                        this.cacheImages.push(cacheImage)
                    }
            }
        },
        updateDataImages(file) {
            this.dataImages.append("file",file,file.name)
        },
        updateDataPdf(e) {
            if (e.target.files) {
                const file = e.target.files[0]
                this.modalData.pdf = file.name
                this.dataPdf.append("file",file,file.name)

            }
        },
        addFeature(e) {
            if ((e.target.id == "otherFeatureTitleForm") && (this.otherFeatureValue == "")) {
                this.$refs.otherFeatureValueInput.focus()
                return
            } else if  ((e.target.id == "otherFeatureValueForm") && (this.otherFeatureTitle == "")) {
                this.$refs.otherFeatureTitleInput.focus()
                return
            }
            
            //Add new feature 
            this.modalData.others.push([this.otherFeatureTitle,this.otherFeatureValue])
            //Reset insertion inputs 
            this.otherFeatureTitle = ""
            this.otherFeatureValue = ""
            
        },
        onModalImagePinTap(blob,image) {
            if (blob == true) {
                this.modalData.fixed_image = this.cacheImages.filter((element) => element.url == image)[0].name
            } else {
                this.modalData.fixed_image = image
            }
        },
        onModalImageTrashTap(blob,image) {
            if (blob == true) {
                this.cacheImages.remove(this.cacheImages.filter((element) => element.url == image)[0])
            } else {
                this.modalData.images.remove(image)
            }
            this.showModal(this.modalData.id)
        },
        onAddButtonTap() {
            this.showModal(undefined)
            this.otherFeatureTitle = ""
            this.otherFeatureValue=""
            this.selectNumber1=0
            this.selection1="fa-solid fa-bath"
            this.selectNumber2=0
            this.selection2="fa-solid fa-bath"
            this.selectNumber3=0
            this.selection3="fa-solid fa-bath"

        },
        onSelectChange(option,number) {
            switch(number) {
                case "1":
                    this.selection1 = option
                    switch(this.selection1) {
                        case "fa-solid fa-bath":
                            this.modalData.bath_rooms = this.selectNumber1
                            break
                        case "fa-solid fa-up-right-and-down-left-from-center":
                            this.modalData.size = this.selectNumber1
                            break
                        case "fa-solid fa-bed":
                            this.modalData.bed_rooms = this.selectNumber1
                            break
                        case "fa-solid fa-tents":
                            this.modalData.green_houses = this.selectNumber1
                            break
                        case "fa-solid fa-stairs":
                            this.modalData.floor = this.selectNumber1
                            break
                        case "fa-solid fa-table":
                            this.modalData.rooms = this.selectNumber1
                            break
                    }
                    break
                case "2":
                    this.selection2 = option
                    switch(this.selection2) {
                        case "fa-solid fa-bath":
                            this.modalData.bath_rooms = this.selectNumber2
                            break
                        case "fa-solid fa-up-right-and-down-left-from-center":
                            this.modalData.size = this.selectNumber2
                            break
                        case "fa-solid fa-bed":
                            this.modalData.bed_rooms = this.selectNumber2
                            break
                        case "fa-solid fa-tents":
                            this.modalData.green_houses = this.selectNumber2
                            break
                        case "fa-solid fa-stairs":
                            this.modalData.floor = this.selectNumber2
                            break
                        case "fa-solid fa-table":
                            this.modalData.rooms = this.selectNumber2
                            break
                    }
                    break
                case "3":
                    this.selection3 = option
                    switch(this.selection3) {
                        case "fa-solid fa-bath":
                            this.modalData.bath_rooms = this.selectNumber3
                            break
                        case "fa-solid fa-up-right-and-down-left-from-center":
                            this.modalData.size = this.selectNumber3
                            break
                        case "fa-solid fa-bed":
                            this.modalData.bed_rooms = this.selectNumber3
                            break
                        case "fa-solid fa-tents":
                            this.modalData.green_houses = this.selectNumber3
                            break
                        case "fa-solid fa-stairs":
                            this.modalData.floor = this.selectNumber3
                            break
                        case "fa-solid fa-table":
                            this.modalData.rooms = this.selectNumber3
                            break
                    }
                    break
                
            }
        },
        deleteData(id) {
            const i = this.data.map(e => e.id).indexOf(id)
            this.data.remove(this.data[i])
            this.cells.remove(id)
        },
        setFixedData(id) {
            const i = this.data.map(e => e.id).indexOf(id)
            this.data[i].on_home = this.data[i].on_home == true ? false : true
            this.isModalVisible = false
        },
        //Flash messages methods
        fetchFlashData() {
            axios.get('/get-flash-dic')
            .then(response => {
            this.currentUser = "Bentornato ".concat(response.data.currentUser);
            this.registerErrorBannerMessage = response.data.registerErrorBannerMessage
            this.changePasswordErrorBannerMessage = response.data.changePasswordErrorBannerMessage
            this.registerSuccessBannerMessage = response.data.registerSuccessBannerMessage
            this.changePasswordSuccessBannerMessage = response.data.changePasswordSuccessBannerMessage
            this.pushDataSuccessBannerMessage = response.data.pushDataSuccessBannerMessage
            this.pushDataErrorBannerMessage = response.data.pushDataErrorBannerMessage
            if (this.registerErrorBannerMessage != undefined || this.changePasswordErrorBannerMessage != undefined ||this.registerSuccessBannerMessage != undefined || this.changePasswordSuccessBannerMessage != undefined) {
                document.getElementsByClassName("admin-form")[0].scrollIntoView()
            }    
            this.deleteFlashDic()
            })
            .catch(error => {
            console.error(error);
            });
        },
        deleteFlashDic() {
            axios.get('/delete-flash-dic')
            .catch(error => {
            console.error(error);
            });
        },
        fetchData() {
            axios.get('/api-admin-fetch')
            .then(response => {
                this.data = response.data
                this.cells = this.data.map((x) => x.id)
            })
            .catch(error => {
            console.error(error);
            });

            axios.get('/get-server-images')
            .then(response => {
                this.serverImages = response.data
            })
            .catch(error => {
            console.error(error);
            });
            axios.get('/get-server-pdf')
            .then(response => {
                this.serverPdf = response.data
            })
            .catch(error => {
            console.error(error);
            });
        }, 
        //Save methods
        pushData() {
            this.data.sort((a, b) => this.cells.indexOf(a.id) - this.cells.indexOf(b.id));
            for (const cacheImage of this.cacheImages) {
                if (this.serverImages.includes(cacheImage.name)) {
                    this.pushDataErrorBannerMessage = "Un file con il nome:'".concat(cacheImage.name).concat("' è già presente sul server. Cancella l'immagine rinominala diversamente e riprova.")            
                    var d = this.dataImages.getAll("file")
                    this.dataImages.delete("file")
                    d = d.map(item => {
                        // Use filter to remove the object with matching id
                        return item.name !== cacheImage.name ? item : null;
                    }).filter(item => item !== null)
                    for (const element of d) {
                        this.dataImages.append("file",element,element.name)
                    }

                    return
                }
            }

            for (const file of this.dataPdf.entries()) {
                if (this.serverPdf.includes(file[1].name)) {
                    this.pushDataErrorBannerMessage = "Un file con il nome:'".concat(file[1].name).concat("' è già presente sul server. Rinomina il pdf e riprova.")            
                    this.dataPdf.delete("file")
                    return
                }
            }
            
            
            for (let i = 0; i < this.data.length; i++) {
                for (const cacheImage of this.cacheImages) {
                    if (cacheImage.id == this.data[i].id) {
                        this.data[i].images.push(cacheImage.name)
                    }
                }
            }
            this.cacheImages = []
            console.log(this.data)
            axios.post('/api-admin-push', this.data)
            .then(response => {
                response
                //post images file from dataImages
                axios.post(
                    '/upload-image', 
                    this.dataImages,
                    {headers: {
                    'Content-Type': 'multipart/form-data'
                    }}
                )
                .then(r => {
                    r
                    axios.post(
                        '/upload-pdf', 
                        this.dataPdf,
                        {headers: {
                        'Content-Type': 'multipart/form-data'
                        }}
                    )
                    .then(r => {
                        r
                        location.reload()
                    })
                })
            })
            .catch(error => {
            console.error(error);
            });
            
            
        }
    },
    computed : {
        registerErrorMessageIsVisible() {
            return this.registerErrorBannerMessage != undefined 
        },
        changePasswordErrorMessageIsVisible() {
            return this.changePasswordErrorBannerMessage != undefined
        },
