<template>
 <label for="image-input">  
    <VStack>
        <i class="fa-solid fa-plus"></i>
        <input type="file" name="" id="image-input" multiple @change="handleFileChange">    
    </VStack>
</label>
    
</template>

<script>
import VStack from './VStack.vue';

 export default {
    props:{
        superCacheImages:[]
    },
    watch: {
        superCacheImages: {
            handler(newValue) {
                this.cacheImages = [] 
                for (const element of newValue) {
                    this.cacheImages.push(element)
                }
            },
            deep:true
        }
    },
    components: {
        VStack,
    },
    methods: {
        handleFileChange(e) {
            if (e.target.files) {
                for (const file of e.target.files) {
                    const url = URL.createObjectURL(file)
                    this.cacheImages.push({name:file.name,url:url})
                    this.$emit("updateDataImages",file)
                    
                }
                this.$emit("updateCacheImages",this.cacheImages)
            }
        }
    },
    data() {
        return {
            cacheImages:[]
        }
    }
 }
</script>

<style scoped>
div {
    aspect-ratio: 4/3; 
    width: 200px;
    height: 150px;
    text-align: center;
}
div i {
    color: white;
    font-weight: 900;
    font-size: 84px;
    clip-path: circle();
    background-color: #1a4169;
}

input[type="file"] {
    display: none;
}
</style>