<template>
<div class="ios-segmented-control" >
  <span class="selection" id="selection"></span>
  <div class="option">
    <input type="radio" id="vendita" name="sample" value="vendita">
    <label for="vendita"><span>In Vendita</span></label>
  </div>
  <div class="option">
    <input type="radio" id="affitto" name="sample" value="affitto" checked class="checked">
    <label for="affitto"><span>In Affitto</span></label>
  </div>
</div>
</template>

<script>
export default {
    props:["value","isModalVisible"],
    watch: {
      isModalVisible(newValue) {
        if (newValue == true) {
          this.shown()
        }
      }
    },
    data() {
      return {
        SEGMENTED_CONTROL_BASE_SELECTOR:".ios-segmented-control",
        SEGMENTED_CONTROL_INDIVIDUAL_SEGMENT_SELECTOR: ".ios-segmented-control .option input",
        SEGMENTED_CONTROL_BACKGROUND_PILL_SELECTOR:".ios-segmented-control .selection"

      }
    },
    mounted() {
      this.forEachElement(this.SEGMENTED_CONTROL_BASE_SELECTOR, elem => {
        elem.addEventListener("change", this.updatePillPosition);
      })
      window.addEventListener("resize", this.updatePillPosition);
    },
    methods: {
      shown(){
        const affitto = document.getElementById("affitto")
        const vendita = document.getElementById("vendita")
        if (this.value == "affitto") {
          vendita.classList.remove("checked")
          vendita.removeAttribute("checked")
          affitto.classList.add("checked")
          affitto.setAttribute("checked","checked")
          document.getElementById("selection").style.transform = "translateX(100%)"
        } else {
          affitto.classList.remove("checked")
          affitto.removeAttribute("checked")
          vendita.classList.add("checked")
          vendita.setAttribute("checked","checked")
          document.getElementById("selection").style.transform = "translateX(0px)"
        }
      },
      updatePillPosition() {
        this.forEachElement(this.SEGMENTED_CONTROL_INDIVIDUAL_SEGMENT_SELECTOR, (elem, index) => {
          if (elem.checked) {
              this.moveBackgroundPillToElement(elem, index)
              elem.classList.add("checked")
              this.$emit("update",elem.value)
          } else {
              elem.classList.remove("checked")
          }
        })
      },
      moveBackgroundPillToElement(elem, index) {
        document.querySelector(this.SEGMENTED_CONTROL_BACKGROUND_PILL_SELECTOR).style.transform = "translateX(" + (elem.offsetWidth * index) + "px)";
      },
      forEachElement(className, fn) {
        Array.from(document.querySelectorAll(className)).forEach(fn)
      }


    }
}

</script>


<style scoped>
.ios-segmented-control {
  --background: #1a4169;
  background: var(--background);
  border-radius: 26px;
  padding: 2px;
  border: none;
  outline: none;
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 1fr;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.ios-segmented-control .option {
  position: relative;
  cursor: pointer;
}
.ios-segmented-control .option:active input:not(:checked) + label span {
  transform: scale(.95);
}
.ios-segmented-control .option label {
  position: relative;
  display: block;
  text-align: center;
  padding: 10px;
  background: rgba(255,255,255,0);
  font-weight: bold;
  color: white;
  font-size: 18px;
}

input.checked ~ label span{
    color: #1a4169;
}


.ios-segmented-control .option label::after {
  right: 0;
  transform: translateX(.5px);
}
.ios-segmented-control .option:first-of-type {
  grid-column: 1;
  grid-row: 1;
  box-shadow: none;
}
.ios-segmented-control .option:first-of-type label::before {
  opacity: 0;
}
.ios-segmented-control .option:last-of-type label::after {
  opacity: 0;
}
.ios-segmented-control .option input {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
  border: none;
  opacity: 0;
}
.ios-segmented-control .selection {
  background: white;
  box-shadow: 0 3px 8px 0 rgba(0,0,0,0.12), 0 3px 1px 0 rgba(0,0,0,0.04);
  border-radius: 26px;
  grid-column: 1;
  grid-row: 1;
  z-index: 2;
  will-change: transform;
  -webkit-transition: transform .2s ease;
  transition: transform .2s ease;
}
.ios-segmented-control .option label span {
  display: block;
  position: relative;
  z-index: 2;
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
  will-change: transform;
}
.ios-segmented-control .option input:checked+label::before, .ios-segmented-control .option input:checked+label::after {
  background: var(--background);
  z-index: 1;
}
.ios-segmented-control .option input:checked+label {
  cursor: default;
}


</style>