<template>
    <form method="POST" action="/login">
            <VStack>
                <HStack><SpacerVue/><img src="../assets/logo.png" alt=""><SpacerVue/></HStack>
                <HStack><SpacerVue/><ErrorBanner v-show="errorBannerIsVisible"><template v-slot:body>{{ errorBannerMessage }}</template></ErrorBanner><SpacerVue/></HStack>
                <HStack><SpacerVue/><input type="text" placeholder="Nome Utente" name="nm"><SpacerVue/></HStack>
                <HStack><SpacerVue/><input type="password" placeholder="Password" name="pw"><SpacerVue/></HStack>
                <HStack><SpacerVue/><input type="submit" name="submit" value="Login"><SpacerVue/></HStack>
                
                
                
            </VStack>
        
        
    </form>
</template>

<script>
import VStack from "@/components/VStack.vue";
import HStack from "@/components/HStack.vue";
import SpacerVue from "@/components/SpacerVue.vue"
import ErrorBanner from "@/components/ErrorBanner.vue"
import axios from "axios";

export default {
    mounted()
    {
        this.fetchFlashData()
    },
    components: {
        HStack,
        VStack,
        SpacerVue,
        ErrorBanner
    },
    computed: {
        errorBannerIsVisible() {
            return this.errorBannerMessage != undefined
        }
    },
    data() {
        return {
            errorBannerMessage:""
        }
    },
    methods: {
        fetchFlashData() {
            axios.get('/get-flash-dic')
            .then(response => {
            this.errorBannerMessage = response.data.errorBannerMessage
            console.log(this.errorBannerMessage)
            this.deleteFlashDic()
            })
            .catch(error => {
            console.error(error);
            });
        },
        deleteFlashDic() {
            axios.get('/delete-flash-dic')
            .catch(error => {
            console.error(error);
            });
        }
    }
}
</script>

<style scoped>
input {
    margin: 12px 0px;
    width: 250px;
    padding: 10px;
    font-size: 20px;
    border: 1px solid lightgray;
    border-radius: 40px
}
input[type="submit"] {
    width: 274px;
    border-color:  #1a4169 ;
    color: white;
    background-color: #1a4169;
}
</style>
