<template>
    <HStack class="admin-cell-hstack" draggable="true" @dragover.prevent="over" @dragleave.prevent="leave" @dragstart="start" @drop.prevent="drop" :id="id">
        <img :src="imagePath" draggable="false"> <h1>{{ title }}</h1>
        <SpacerVue/>
        <i @click.stop="onPinTap" :class="on_home ? 'fa-solid fa-thumbtack selected' : 'fa-solid fa-thumbtack'"></i>
        <i class="fa-solid fa-trash-can" style="color: #cc0202;" @click.stop="onTrashTap"></i>

    </HStack>
</template>


<script>
import HStack from "./HStack.vue";
import SpacerVue from "./SpacerVue.vue";


export default {
    props: ["id", "cells","title","image","on_home"],
    components : {
    HStack,
    SpacerVue
    },
    methods: {
        start(e) {
            const target = e.target
            e.dataTransfer.setData("cell_id",target.id)
            console.log(`Drag start at ${this.id}`)
        },
        drop(e) {
            console.log(`Drop start at ${this.id}`)
            const cellID = e.dataTransfer.getData("cell_id")
            //emit remove and add
            const src = this.cells.findIndex((x) => {
                return x == cellID
            })
            const dst = this.cells.findIndex((x) => {
                return x == this.id
            })

            this.$emit("moveCell",src,dst)

            const target = e.target
            target.style["border-top-color"] = "#e6e6e6"



        },
        over(e) {
            const target = e.target
            target.style["border-top-color"] = "#1a4169"
        },
        leave(e){
            const target = e.target
            target.style["border-top-color"] = "#e6e6e6"
        },
        onTrashTap() {
            this.$emit("deleteData",this.id)
        },
        onPinTap() {
            this.$emit("setFixedData",this.id)
        }
    },
    computed: {
        imagePath() {
            return `/images/`.concat(this.image)
        }
    }
}
</script>


