<template>
    <VStack class="grid-cell" @click="$router.push('/details?id='.concat(immobile.id))">
        <img :src="'/images/'.concat(immobile.fixed_image)" alt="" style="border-radius: 5% 5% 0% 0%;">
        <VStack class="title-stack">
            <div class="title">{{ immobile.title }}</div>
            <div class="price">{{ immobile.price > 0 ? "€".concat(Intl.NumberFormat().format(immobile.price)): "Trattativa Riservata" }}</div>
        </VStack>
        <HStack class="location-stack">
            <i class="bi bi-geo-alt-fill"></i>{{ immobile.city }}<SpacerVue/>
        </HStack>
        <HStack class="info-stack">
            <SpacerVue/>
            <InfoCell :icon="info[0][0]" :info="info[0][1]"/>
            <SpacerVue/>
            <InfoCell :icon="info[1][0]" :info="info[1][1]"/>
            <SpacerVue/>
            <InfoCell :icon="info[2][0]" :info="info[2][1]"/>
            <SpacerVue/>
        </HStack>

        <HStack class="overlay">
            <VStack class="overlay-text" style="background-color: black;"><SpacerVue/>{{ immobile.tipology }}<SpacerVue/></VStack>
            <VStack class="overlay-text" style="background-color: #1a4169;"><SpacerVue/>{{ immobile.category }}<SpacerVue/></VStack>
        </HStack>
    </VStack>
</template>

<script>
import HStack from "./HStack.vue"
import VStack from "./VStack.vue"
import SpacerVue from "./SpacerVue.vue"

import InfoCell from "./InfoCell.vue"

export default {
    props:{
        immobile: {

        }
    },
    components: {
        HStack,
        VStack,
        InfoCell,
        SpacerVue
    },
    computed: {
        info() {
            var info = []
            if (this.immobile.green_houses > 0) {
                info.push(["fa-solid fa-tents",this.immobile.green_houses])
            }
            if (this.immobile.rooms > 0) {
                info.push(["fa-solid fa-table",this.immobile.rooms])
            }   
            if (this.immobile.bath_rooms > 0) {
                info.push(["fa-solid fa-bath",this.immobile.bath_rooms])
            }
            if (this.immobile.bed_rooms > 0) {
                info.push(["fa-solid fa-bed",this.immobile.bed_rooms])
            }
            if (this.immobile.size > 0) {
                info.push(["fa-solid fa-up-right-and-down-left-from-center",String(this.immobile.size).concat("m²")])
            }
            if (this.immobile.floor > 0) {
                info.push(["fa-solid fa-stairs",this.immobile.floor])
            }
            return info
        }
    }
}

</script>

