<template>
    <HStack class="bottom-bar hidden">
        <a href="#email-form" class="mail"><i class="fa-solid fa-envelope"></i> Invia un'email</a>
        <a href="tel:338-158-9664" class="phone"><i class="fa-solid fa-phone"></i> Chiamaci ora</a>
    </HStack>
</template>



<script>
import HStack from './HStack.vue';

export default {
    created() {
        window.addEventListener("scroll",this.resizeInfo)
    },
    components: {
        HStack
    },
    methods: {
        resizeInfo() {
            const bottomBar = document.getElementsByClassName("bottom-bar")[0]
            if (window.scrollY > window.innerHeight - 350) {
                bottomBar.classList.remove("hidden")
            } else {
                bottomBar.classList.add("hidden") 
            }
        }
    }
}

</script>


<style scoped>

.bottom-bar {
    position: fixed;
    bottom:  0px;
    width: 100%;
    z-index: 10000;
    transition: 0.5s transform ease-in-out;
    transform: translateY(0px);
}
.bottom-bar.hidden {
    transform: translateY(75px);
}

.bottom-bar a {
    width: 50%;
    text-align: center;
    line-height: 75px;
    text-decoration: none;
    font-weight: bolder;
    font-size: 16px;
}
.bottom-bar a.phone {
    color: #1a4169;
    background-color: white;
}
.bottom-bar a.mail {
    color: white;
    background-color: #1a4169;
}
.bottom-bar i {
    transform: translateY(2px);
}
</style>