<template>
  <TopBar v-show="displayHeaders"/>
  <router-view id="router-view"/>
  <HeaderVue id="header" v-show="displayHeaders"/>
  <BottomBar v-show="displayHeaders"/>
</template>

<script>
// import HStack from "./components/HStack.vue"
import TopBar from "./components/TopBar.vue"
import BottomBar from "./components/BottomBar.vue"
import HeaderVue from "./components/HeaderVue.vue"

export default {
  components: {
    TopBar,
    BottomBar,
    HeaderVue
  },
  watch: {
    $route: function() {
      // Check if given route is true, if it is then hide Nav. 
      if (this.$route.path === "/admin" || this.$route.path === "/login") {
          this.displayHeaders = false
          } else  {
            this.displayHeaders = true
      }
    }
  },
  methods: {
    setup() {
      if (this.$route.path === "/admin" || this.$route.path === "/login") {
          this.displayHeaders = false
          } else  {
            this.displayHeaders = true
      }
    }
  },
  data() {
    return {
      displayHeaders:true
    }
  }
}
</script>


<style>
#router-view {
  padding-top: 200px;
  overflow-x: hidden;
}
#header {
  padding-bottom: 75px;
}
</style>