import HStack from './HStack.vue';
import SpacerVue from './SpacerVue.vue';
import {Row, Column} from "vue-grid-responsive"


export default {
    created() {
        window.addEventListener("scroll",this.resizeInfo)
    },
    data() {
        return {
            mobileMenuClass:""
        }
    },
    components: {
        VStack,
        HStack,
        SpacerVue,
        Row,
        Column
    },
    methods: {
        resizeInfo() {
            const mobileInfo = document.getElementsByClassName("top-bar-info-mobile")[0]
            if (window.scrollY > 100) {
                mobileInfo.classList.add("hidden") 
            } else {
                mobileInfo.classList.remove("hidden")
            }
        }
    }
}



