<template>
    <VStack>
        <DetailsImage @showModal="showModal" :data="data"/>
        <VStack dynamic="hstack@md" class="details-container">
            <VStack class="description">
                <HStack>
                    <SpacerVue/>
                    <h1 class="section-title">Descrizione</h1>
                    <SpacerVue/>
                </HStack>
                <p>{{ data.description }}</p>
                <SpacerVue/>
            </VStack>
            <VStack class="info">
                <h1>{{ data.price > 0 ? "€".concat(Intl.NumberFormat().format(data.price)): "Trattativa Riservata" }}</h1>
                <HStack id="info-stack">
                    <SpacerVue/>
                    <InfoCell :icon="info[0][0]" :info="info[0][1]"/>
                    <SpacerVue/>
                    <InfoCell :icon="info[1][0]" :info="info[1][1]"/>
                    <SpacerVue/>
                    <InfoCell :icon="info[2][0]" :info="info[2][1]"/>
                    <SpacerVue/>
                    </HStack>
                <h3>Caratteristiche</h3>
                <VStack class="info-table">
                    <HStack><strong>Tipologia</strong><SpacerVue/>{{ data.tipology }}</HStack>
                    <HStack><strong>Contratto</strong><SpacerVue/>{{ data.category }}</HStack>
                    <HStack><strong>Città</strong><SpacerVue/>{{ data.city }}</HStack>
                    <HStack><strong>Indirizzo</strong><SpacerVue/>{{ data.address }}</HStack>
                    <HStack><strong>Zona</strong><SpacerVue/>{{ data.zone }}</HStack>
                    <HStack v-for="info in data.others" :key="info[0]"><strong>{{info[0]}}</strong><SpacerVue/>{{ info[1] }}</HStack>
                </VStack>
                <SpacerVue/>
            </VStack>
        </VStack>
        <!--Modals Views-->
        <!--Carousel Modal-->
        <ModalVue
            class="modal-carousel"
            v-show='isModalVisible["carousel"]' 
            @close='closeModal("carousel")' 
            v-if="data.images.length > 0"
            >

            <template v-slot:body>
                <carousel :slides="data.images.map(el => '/images/'.concat(el))"  controls indicators></carousel>
            </template>
        </ModalVue>
        <!--Video Modal-->
        <ModalVue
            class="modal-video"
            v-show='isModalVisible["video"]' 
            @close='closeModal("video")' 
            v-if="data.video != ''"
            >

            <template v-slot:body>
                <iframe class="yt-video" :src="data.video" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
            </template>
        </ModalVue>
        <!--Plan Modal-->
        <ModalVue
            class="modal-plan"
            v-show='isModalVisible["plan"]' 
            @close='closeModal("plan")' 
            v-if="data.pdf != ''"
            >

            <template v-slot:body>
                <iframe src="/pdf/vue.pdf" frameborder="0" style="width: 100%;height: 90vh;"></iframe>
                
            </template>
        </ModalVue>
        <!--Maps Modal-->
        <ModalVue
            class="modal-maps"
            v-show='isModalVisible["maps"]' 
            @close='closeModal("maps")'
            v-if="data.maps != ''" 
            >

            <template v-slot:body>
                <GoogleMap class="map-frame" :src="data.maps"  style="border:0;width: 100%;height: 90vh;" loading="lazy" referrerpolicy="no-referrer-when-downgrade" />
            </template>
        </ModalVue>
        <!--StreetView Modal-->
        <ModalVue
            class="modal-streetView"
            v-show='isModalVisible["streetView"]' 
            @close='closeModal("streetView")' 
            v-if="data.street_view != ''"
            >

            <template v-slot:body>
                <GoogleMap class="map-frame" :src="data.street_view"  
                style="border:0;width: 100%;height: 90vh;" loading="lazy" referrerpolicy="no-referrer-when-downgrade" />
            </template>
        </ModalVue>
    </VStack>

</template>

<script>
import DetailsImage from '@/components/DetailsImage.vue';
import HStack from '@/components/HStack.vue';
import InfoCell from '@/components/InfoCell.vue';
import SpacerVue from '@/components/SpacerVue.vue';
import VStack from '@/components/VStack.vue';
import ModalVue from '@/components/Modal.vue';
import GoogleMap from '@/components/GoogleMap.vue';
import Carousel from '@/components/CarouselVue.vue';
import axios from 'axios';






export default {
    components: {
    DetailsImage,
    VStack,
    SpacerVue,
    HStack,
    InfoCell,
    ModalVue,
    Carousel,
    GoogleMap

    },
    computed: {
        info() {
            var info = []
            if (this.data.green_houses > 0) {
                info.push(["fa-solid fa-tents",this.data.green_houses])
            }
            if (this.data.rooms > 0) {
                info.push(["fa-solid fa-table",this.data.rooms])
            }   
            if (this.data.bath_rooms > 0) {
                info.push(["fa-solid fa-bath",this.data.bath_rooms])
            }
            if (this.data.bed_rooms > 0) {
                info.push(["fa-solid fa-bed",this.data.bed_rooms])
            }
            if (this.data.size > 0) {
                info.push(["fa-solid fa-up-right-and-down-left-from-center",String(this.data.size).concat("m²")])
            }
            if (this.data.floor > 0) {
                info.push(["fa-solid fa-stairs",this.data.floor])
            }
            return info
        }
    },
    data() {
        return {
            isModalVisible:{
                "carousel":false,
                "video":false,
                "plan":false,
                "maps":false,
                "streetView":false
            } ,
            numPages:0,
            pdfData:undefined,
            data:{}
        }
    },
    methods: {
        showModal(modal) {
            this.isModalVisible[modal] = true
            
        },
        closeModal(modal) {
            this.isModalVisible[modal] = false
        }
    },
    mounted() {
        axios.get("/api-details/".concat(this.$route.query.id))
        .then(response => {
            this.data = response.data
            console.log(this.data)
        })
        .catch(error => {
            console.error(error);
        });
        
    }
    
}

</script>



