<template>
<div style="position: relative; width: 200px; z-index: 999;">
    <img :src="imagePath" alt="" style="width: inherit;">
    <div class="overlay-left"><i class="fa-solid fa-thumbtack" @click="onPinTap"></i></div>
    <div class="overlay-right"><i class="fa-solid fa-trash-can" @click="onTrashTap"></i></div>

</div>
</template>


<script>
export default {
    props:[ "image","blob"],
    computed: {
        imagePath() {
            if (this.blob == true) {
                return this.image
            } else {
                return `/images/`.concat(this.image)
            }
            
        }
    },
    methods: {
        onTrashTap() {
            this.$emit("onModalImageTrashTap",this.blob,this.image)
        },
        onPinTap() {
            this.$emit("onModalImagePinTap",this.blob,this.image)
        }
    }

}
</script>


